  mounted(){
    //可用jquery方法
    // $.ajax({
    //   type:"get",
    //   url:"http://127.0.0.1:1718/indexjson",
    //   dataType:"json",
    //   success:function(data){
    //     console.log(data)
    //     var html=''
    //     for(let i=0,len=data.length;i<len;i++){
    //       html+='<div class="col-sm-6 col-md-4"><div class="thumbnail"><img src="http://127.0.0.1:1718/'
    //       +data[i].firstimg+
    //       '" alt="..." style="height:200px;width:100%"><div class="caption"><h3 class="ell">'
    //       +data[i].title+
    //       '</h3><p><a href="#" target="_blank">阅读全文</a></p></div></div></div>'
    //     }
    //     $(".row").append(html)
    //   }
    // })



    //axios
    this.$ajax("http://127.0.0.1:1718/indexjson")
                            .then(res=>{
                                console.log(res.data[0])
                                this.main=res.data
              })

  }
};
</script>

<style>
.block {
  margin-bottom: 10px;
}
</style>




