<template>
    <div>
        <Nav></Nav>
        <div class="container"></div>
    </div>
</template>


<script>
import Nav from '../nav/nav';
export default {
  components:{ Nav },
  mounted(){
      $.ajax({
          type:"get",
          url:"http://127.0.0.1:1718/indexjson",
          dataType:"json",
          success:function(data){
              $(".container").append(data[0].main)
            console.log($(data[0].main))
          }
      })
  }
};
</script>

<style>
.block {
  margin-bottom: 10px;
}
</style>




